export class Observer {
    constructor() {
        this.eventListeners = new Map();
    }
    /**
     * 监听事件
     * @param channel
     * @param handler
     */
    on(channel, handler) {
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            listeners.add(handler);
        }
        else {
            this.eventListeners.set(channel, new Set([handler]));
        }
        return this;
    }
    /**
     * 发送事件
     * @param channel
     * @param data
     */
    emit(channel, data) {
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            for (const listener of listeners) {
                listener(data);
            }
        }
        return this;
    }
    /**
     * 为给定事件添加一次性监听器。
     * @param channel 频道
     * @param listener 事件回调
     * @returns
     */
    once(channel, handler) {
        const _handle = (ev, arrayBuffer) => {
            this.clear(channel, _handle);
            handler.call(this, ev, arrayBuffer);
        };
        this.on(channel, _handle);
        return this;
    }
    /**
     * 删除监听的事件
     * @param channel
     * @param handler
     * @returns
     */
    clear(channel, handler) {
        const handlers = this.eventListeners.get(channel);
        if (handlers) {
            return handlers.delete(handler);
        }
        return false;
    }
}
