import { Observer } from "../Observer";
class DecoderAbstract {
}
export class Decoder extends Observer {
    constructor() {
        super();
        /**
         * 所有帧已成功解码完成
         */
        this.decoderImageComplete = false;
        /**
         * 文件解析完成
         */
        this.decoderParseComplete = false;
        /**
         * 解码器初始化完成
         */
        this.decoderInitial = false;
        /**
         * 解码器版本
         */
        this.decoderVersion = "";
        /**
         * 帧数
         */
        this.imageCount = 0;
    }
    decoderParse(arrayBuffer) {
        return Promise.resolve(true);
    }
    decoderNthImage(frameIndex) {
        return Promise.resolve({});
    }
}
export class MainEventEmitter extends Decoder {
    constructor(url) {
        super();
        this.workerListeners = new Map();
        this.worker = new Worker(url);
        this.worker.onmessage = (ev) => {
            this.listenOnmessage(ev);
        };
    }
    /**
     * 发送事件到Worker线程
     * @param channel
     * @param data
     * @param args
     */
    postMessage(channel, data, arrayBuffer) {
        if (data instanceof ArrayBuffer) {
            this.worker.postMessage([channel, data], [data]);
        }
        else {
            if (arrayBuffer instanceof ArrayBuffer) {
                this.worker.postMessage([channel, data, arrayBuffer], [arrayBuffer]);
            }
            else {
                this.worker.postMessage([channel, data]);
            }
        }
    }
    /**
     * 为给定的Worker线程事件添加一次性侦听器。
     * @param channel 频道
     * @param handler 事件回调
     * @returns
     */
    onmessageOnce(channel, handler) {
        const _handle = (ev, arrayBuffer) => {
            handler.call(this, ev, arrayBuffer);
            this.clearOnmessage(channel, _handle);
        };
        this.onmessage(channel, _handle);
        return this;
    }
    /**
     * 清除Worker线程事件
     * @param channel
     * @param handler
     * @returns
     */
    clearOnmessage(channel, handler) {
        const handlers = this.workerListeners.get(channel);
        if (handlers) {
            return handlers.delete(handler);
        }
        return false;
    }
    /**
     * 监听Worker线程发送的事件
     * @param channel
     * @param handler
     */
    onmessage(channel, handler) {
        if (this.workerListeners.has(channel)) {
            const listeners = this.workerListeners.get(channel);
            listeners.add(handler);
        }
        else {
            this.workerListeners.set(channel, new Set([handler]));
        }
    }
    listenOnmessage(ev) {
        const [channel, data, arrayBuffer] = ev.data;
        if (this.workerListeners.has(channel)) {
            const listeners = this.workerListeners.get(channel);
            for (const listener of listeners) {
                listener(data, arrayBuffer);
            }
        }
    }
}
